        { title:'中电科翌智航（大连）科技有限公司', address:'辽宁省大连经济技术开发区9号办公区发展大厦515', tel:'023-61737007', tel1:'023-61737007 转8004', mailTo:'zdkyzhcq@efly-cetc.com', visible:false, center: { lng: 39.057819, lat: 121.792638 }, mapUrl:'https://map.baidu.com/poi/%E5%8F%91%E5%B1%95%E5%A4%A7%E5%8E%A6/@13558041.173277706,4702775.149245958,21z?uid=9ee392b823102741bbbd6322&info_merge=1&isBizPoi=false&ugc_type=3&ugc_ver=1&device_ratio=2&compat=1&querytype=detailConInfo&da_src=shareurl'},
      ],
      mapUrl:
        "https://map.baidu.com/poi/%E4%B8%AD%E7%94%B5%E7%A7%91%E7%BF%8C%E6%99%BA%E8%88%AA(%E9%87%8D%E5%BA%86)%E7%A7%91%E6%8A%80%E6%9C%89%E9%99%90%E5%85%AC%E5%8F%B8/@11842210.307460086,3432090.4144750657,18.9z?uid=b759b12625bbd0f9c71e51ba&ugc_type=3&ugc_ver=1&device_ratio=2&compat=1&querytype=detailConInfo&da_src=shareurl",
      currentIndex:0,
    };
  },
  watch: {
    currentIndex(index) {
      this.provinces.forEach((item, i) => {
        if (i == index) {
          item.visible = true;
        } else {
          item.visible = false;
        }
      });
      this.$nextTick(()=>{
        this.initMap(this.provinces[this.currentIndex]);
      })
    },
  },
  methods:{
    next() {
      let length = this.provinces.length;
      if (this.currentIndex >= length - 1) {
        return false;
      }
      this.currentIndex++;
    },
    previous() {
      if (this.currentIndex > 0) {
        this.currentIndex--;
      }
    },
    initMap(item){
      let ref = this.$refs.chongqing[0];
     
      let map = new BMapGL.Map(ref);
      let point = new BMapGL.Point(item.center.lat, item.center.lng);
      map.centerAndZoom(point, 19);
      let marker = new BMapGL.Marker(point); // 创建标注
      marker.addEventListener("click", () => {
        window.open(this.mapUrl, "_blank");
      });
      map.addOverlay(marker); // 将标注添加到地图中
    }
  },
  mounted() {
    this.initMap(this.provinces[this.currentIndex]);
    return false;
  },
};
</script>
